/*
* function for determining page buttons displayed on pagination
* returns array of page numbers
*/

export default function (currentPage, totalPages, pageLimit) {
  let pageList = []
  const limitIsEven = pageLimit % 2 === 0
  let listPadding = Math.floor(pageLimit / 2)
  let start = currentPage - listPadding
  let finish = currentPage + listPadding

  if (pageLimit >= totalPages) {
    start = 1
    finish = totalPages
  }

  if (limitIsEven) {
    start++
    listPadding--
  }

  if (start < 1) {
    start += listPadding
    finish += listPadding
  }

  if (finish > totalPages) {
    if (limitIsEven && currentPage === 20) {
      listPadding++
    }

    start -= listPadding
    finish -= listPadding
  }

  for (let i = start; i <= finish; i++) {
    pageList.push(i)
  }

  return pageList
}



