<template lang="html">
  <div class="post">
    <div class="post__header">
      <div class="post__user">
        <div class="post__user__display">
          <figure class="post__user__display__image">
            <img src="http://icons.iconarchive.com/icons/paomedia/small-n-flat/1024/user-male-icon.png" alt="Profile Image">
          </figure>
        </div>
        <div class="post__user__info">
          <p class="post__user__info__name">{{user.name}}</p>
          <p class="post__user__info__username">@{{user.username}}</p>
        </div>
      </div>
      <a class="post__button" @click="openPost(postId)">Edit</a>
    </div>
    <div class="post__title">
      <h3>{{postTitle}}</h3>
    </div>
    <div class="post__body">
      <p>{{postBody}}</p>
    </div>
  </div>
</template>

<script>
import { mapMutations } from 'vuex'

export default {
  props: [
    'user',
    'postTitle',
    'postBody',
    'postId'
  ],
  methods: {
    ...mapMutations({
      openPost: 'OPEN_EDITOR'
    })
  }
}
</script>

<style lang="scss" scoped>
  @import "./bulma/sass/utilities/_all.sass";
  @import "./bulma/sass/grid/columns.sass";
  @import "./bulma/sass/components/media.sass";
  @import "./bulma/sass/elements/image.sass";
  @import "./bulma/sass/elements/title.sass";
  @import "./bulma/sass/elements/button.sass";
  @import "./bulma/sass/components/card.sass";

  .post {
    @extend .card, .column;
    margin-bottom: 1.5em;
    padding: 0;
    border-radius: 3px;

    &__header {
      @extend .card-header;
    }

    &__user {
      @extend .media;

      &__display {
        @extend .media-left;
        align-self: center;
        margin: 0;

        &__image {
          @extend .image, .is-48x48;
          margin-right: 0;
        }
      }

      &__info {
        @extend .media-right;

        &__name {
          @extend .title, .is-4;
        }

        &__username {
          @extend .subtitle, .is-6;
        }
      }

    }

    &__button{
      @extend .button, .is-small, .is-outlined;
      align-self: center;
      margin-left: auto;
      margin: 16px 40px 16px auto;
      padding: .08em 1em;
    }

    &__title {
      @extend .card-content, .title, .is-4;
      background-color: $primary;
      padding: 30px 1em;
      color: $white;
      text-align: center;

      &:not(:last-child) {
        margin: 0;
      }

    }

    &__body {
      @extend .card-content;
      white-space: pre-wrap;
    }

  }
</style>



