<template lang="html">
  <section class="pagination-controller">
    <div class="pagination-controller__info">
      <div class="pagination-controller__info__items-showing">
        <p>Showing {{pageItemRange}} posts of {{totalItems}}</p>
      </div>
    </div>
    <div class="pagination-controller__control">
      <div class="pagination-controller__control__description">
        <p>Posts Per Page:</p>
      </div>
      <div class="pagination-controller__control__input">
        <p>
          <span class="pagination-controller__control__input--select">
            <select v-model='itemsPerPage'>
              <option v-for='option in itemsPerPageOptions' :value="option">{{option}}</option>
            </select>
          </span>
        </p>
      </div>
    </div>
  </section>
</template>

<script>
import Search from '../Search.vue'
export default {
  props: {
    itemsPerPageOptions: {
      type: Array,
      required: false,
      default: function () {
        return [5, 10, 25]
      }
    }
  },
  components: {
    Search
  },
  computed: {
    totalItems () {
      return this.$store.state.posts.total
    },
    itemsPerPage: {
      get () {
        return this.$store.state.pagination.itemsPerPage
      },
      set (itemsPerPage) {
        this.$store.dispatch('changeItemsPerPage', itemsPerPage)
      }
    },
    pageItemRange () {
      const currentPage = this.$store.state.pagination.currentPage
      const itemsPerPage = this.itemsPerPage

      let max = currentPage * itemsPerPage
      const min = currentPage !== 1 ? max - itemsPerPage + 1 : 0

      if (max > this.totalItems) {
        max = this.totalItems
      }
      return min + ' - ' + max
    }
  }
}
</script>

<style lang="scss">
  @import "./bulma/sass/utilities/_all.sass";
  @import "./bulma/sass/elements/form.sass";
  @import "./bulma/sass/elements/box.sass";
  @import "./bulma/sass/components/level.sass";

  .pagination-controller {
    @extend .level;

    &__info {
      @extend .level-left;

      &__items-showing {
        @extend .level-item;
      }
    }

    &__control {
      @extend .level-right;

      &__description {
        @extend .level-item;
      }

      &__input {
        @extend .level-item;

        p {
          @extend .control;
        }

        &--select {
          @extend .select;
        }
      }
    }
  }

</style>



