<template lang="html">
  <nav class="pagination">
    <a v-if="includeNextAndPrev" class="pagination__button--next" :class="getNextButtonClass()" @click="nextPage">Next</a>
    <a v-if="includeNextAndPrev" class="pagination__button--prev" :class="getPrevButtonClass()" @click="previousPage">Previous</a>
    <ul class="pagination__page-list">
      <li v-show="firstPageVisible && pageOptions.length" @click="goToPage(1)">
        <a class="pagination__page-list__button--jump">
          1
        </a>
      </li>
      <li>
      <span v-show="firstPageVisible && pageOptions.length" class="pagination-ellipsis">&hellip;</span>
      </li>
      <li v-for="page in pageOptions" @click="goToPage(page)">
        <a class="pagination__page-list__button" :class="getActivePage(page)">{{page}}</a>
      </li>
      <li v-show="lastPageVisable && pageOptions.length">
      <span class="pagination-ellipsis">&hellip;</span>
      </li>
      <li v-show="lastPageVisable && pageOptions.length" @click="goToPage(numberOfPages)">
        <a class="pagination__page-list__button--jump">{{numberOfPages}}</a>
      </li>
    </ul>
  </nav>
</template>

<script>
import pageList from './pageList'
import {mapState, mapGetters, mapActions} from 'vuex'

export default {
  name: 'Pagination',
  props: {
    pageLimit: {
      type: Number,
      required: false,
      default: 5
    },
    includeNextAndPrev: {
      type: Boolean,
      required: false,
      default: false
    }
  },
  computed: {
    ...mapState({
      currentPage: state => state.pagination.currentPage
    }),
    ...mapGetters([
      'numberOfPages'
    ]),
    pageOptions () {
      return pageList(this.currentPage, this.numberOfPages, this.pageLimit)
    },
    firstPageVisible () {
      return this.pageOptions.indexOf(1) === -1
    },
    lastPageVisable () {
      return this.pageOptions.indexOf(this.numberOfPages) === -1
    }
  },
  methods: {
    ...mapActions([
      'goToPage',
      'nextPage',
      'previousPage'
    ]),
    getNextButtonClass () {
      return this.currentPage !== this.numberOfPages ? null : 'is-disabled'
    },
    getPrevButtonClass () {
      return this.currentPage !== 1 ? null : 'is-disabled'
    },
    getActivePage (pageNumber) {
      return pageNumber !== this.currentPage ? null : 'is-current'
    }
  }
}
</script>

<style lang="scss" scoped>
    @import "./bulma/sass/utilities/variables.sass";
    @import "./bulma/sass/utilities/mixins.sass";
    @import "./bulma/sass/utilities/controls.sass";
    @import "./bulma/sass/components/pagination.sass";

    %jump-link-style {
      color: $grey;
    }

    .pagination {
      @extend .is-centered;
      &__button {
        &--prev{
          @extend .pagination-previous;
        }
        &--next{
          @extend .pagination-next;
        }
      }

      &__page-list{
        @extend .pagination-list;

        &__button {
          @extend .pagination-link;

          &--jump {
            @extend .pagination-link;
            @extend %jump-link-style;
          }
        }
      }

    }

    ul{
      list-style-type: none;
    }

</style>



