<template lang="html">
  <section class="post-feed">
    <div class="post-feed__posts">
      <post  v-for="post in posts"
            :user="post.user"
            :post-title="post.title"
            :post-body="post.body"
            :post-id="post.id"
            :key="post.id">
      </post>
      <span v-show="loading" class="loader"></span>
      <div v-show="noResults" class="post-feed__no-result-message">
        Sorry! :(  No Posts were found
      </div>
    </div>
  </section>
</template>

<script>
import {mapActions} from 'vuex'
import Post from './Post'

export default {
  components: {
    Post
  },
  computed: {
    posts () {
      return this.$store.state.posts.data
    },
    loading () {
      return this.$store.state.posts.areLoading
    },
    noResults () {
      return this.posts < 1 && !this.loading
    }
  },
  methods: mapActions([
    'deletePost'
  ]),
  created () {
    this.$store.dispatch('fetchPosts')
  }
}
</script>

<style lang="scss" scoped>
  @import "./bulma/sass/utilities/_all.sass";
  @import "./bulma/sass/grid/columns.sass";
  @import "./bulma/sass/elements/notification.sass";
  @import "./bulma/sass/elements/other.sass";

  .loader {
    font-size: 2em;
    margin: auto;
  }

  .post-feed{
    @extend .columns;

    &__posts {
      @extend .column, .is-8, .is-offset-2;
    }

    &__no-result-message {
      @extend .notification;
      text-align: center;
    }
  }

</style>



