import Vue from 'vue'
import Vuex from 'vuex'
import mutations from './mutation-types'
import * as actions from './actions'
import * as getters from './getters'

Vue.use(Vuex)

export default new Vuex.Store({
  state: {
    posts: {
      total: 0,
      data: [],
      areLoading: false
    },
    editor: {
      postEditing: null
    },
    pagination: {
      currentPage: 1,
      itemsPerPage: 5
    },
    search: {
      input: ''
    }
  },
  mutations: {
    [mutations.MODIFY_POST] (state, post) {
      const postIndex = state.posts.data.findIndex(index => { return index.id === post.id })
      console.log(postIndex)

      Object.assign(state.posts.data[postIndex], post)
    },
    [mutations.UPDATE_POSTS] (state, posts) {
      state.posts.data = posts.data
      state.posts.total = posts.total
      state.posts.areLoading = false
    },
    [mutations.SET_LOADING_STATUS] (state, status) {
      state.posts.areLoading = status
    },
    [mutations.CHANGE_PAGE] (state, pageNumber) {
      state.pagination.currentPage = pageNumber
    },
    [mutations.CHANGE_ITEMS_PER_PAGE] (state, itemsPerPage) {
      state.pagination.itemsPerPage = itemsPerPage
      state.pagination.currentPage = 1
    },
    [mutations.UPDATE_SEARCH] (state, value) {
      state.search.input = value
    },
    [mutations.OPEN_EDITOR] (state, postId) {
      const posts = state.posts.data
      for (let i = 0; i < posts.length; i++) {
        if (posts[i].id === postId) {
          state.editor.postEditing = posts[i]
        }
      }
    },
    [mutations.CLOSE_EDITOR] (state) {
      state.editor.postEditing = null
    }
  },
  getters,
  actions
})



