<template lang="html">
  <section :class="getEditorState()" class="post-editor is-active">
    <div class="post-editor__background" @click="closeEditor"></div>
    <div class="post-editor__content">
      <editor :post="post">
      </editor>
    </div>
    <button class="post-editor__close" type="button" @click="closeEditor"></button>
  </section>
</template>

<script>
import { mapState, mapMutations } from 'vuex'
import Editor from './Editor'

export default {
  components: {
    Editor
  },
  computed: {
    ...mapState({
      post: state => state.editor.postEditing
    })
  },
  methods: {
    getEditorState () {
      return this.post !== null ? 'is-active' : ''
    },
    ...mapMutations({
      closeEditor: 'CLOSE_EDITOR'
    })
  },
  beforeMount () {

  }
}
</script>

<style lang="scss">
  @import "./bulma/sass/utilities/_all.sass";
  @import "./bulma/sass/components/modal.sass";

  .post-editor{
    @extend .modal;

    &__background{
      @extend .modal-background;
    }

    &__content {
      @extend .modal-content;
    }

    &__close {
      @extend .modal-close;
    }
  }
</style>



