<template lang="html">
    <div class="search">
      <p class="control">
        <input class="search__input" type="text" v-model="input" placeholder="SEARCH POSTS">
      </p>
    </div>
</template>

<script>
export default {
  computed: {
    input: {
      get () {
        return this.$store.state.search.input
      },
      set (value) {
        this.$store.commit('UPDATE_SEARCH', value)
        this.$store.commit('CHANGE_PAGE', 1)
        this.$store.dispatch('fetchPosts')
      }
    },

    numberOfResults () {
      return this.$store.state.posts.total
    }
  }
}
</script>

<style lang="scss" scoped>
  @import "./bulma/sass/utilities/_all.sass";
  @import "./bulma/sass/grid/columns.sass";
  @import "./bulma/sass/elements/form.sass";

  .search {
    @extend .column, .is-6, .is-offset-3;

    &__input {
      @extend .is-small, .input;
    }
  }


</style>



